import React from "react";
import { Link } from "react-router-dom";
import { Navbar, Nav, Container, NavDropdown } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";


const Header = () => {
  return (
    <Navbar bg="dark" variant="dark" expand="md" className="py-3">
      <Container>
        <Navbar.Brand as={Link} to="/">
          UPLEVEL 3D
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ms-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/figures">Figures</Nav.Link>
            <Nav.Link as={Link} to="/decoracao">Decoração</Nav.Link>
            <Nav.Link as={Link} to="/contato">Contato</Nav.Link>
            <a className="text-light text-decoration-none" href="https://instagram.com/uplevel3d" target="_blank" rel="noreferrer">Instagram</a>
            <a className="text-light text-decoration-none" href="https://youtube.com/@uplevel3d" target="_blank" rel="noreferrer">YouTube</a>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default Header;
